import React from 'react'
import styled from 'styled-components'
import { Input } from '../globalStyles'


const Form = styled.form`
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 45px;
    @media (max-width: 910px) {
        flex-direction: column;
        width: 100%;
        margin-bottom: 20px;
    }
`

const Submit = styled(Input)`
    background: #C9D388;
    border-radius: 4px;
    margin-right: 5px;
    @media (max-width: 910px) {
        width: 100%;
        max-width:100%;
    }
`

const Select = styled.select`
    width: 235px;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    background: #FFFFFF;
    padding: 10px 15px 10px 15px;
    border: 1px solid #BDBDBD;
    border-radius: 4px;
    margin-right: 10px;
    @media (max-width: 910px) {
        width: 100%;
        max-width:100%;
        margin-bottom: 5px;
    }
`

const FormShopping = () => {
    return (
        <Form action="">
            <Select>
                <option value="" selected>Тип покупки</option>
                <option value="еда">Eда</option>
                <option value="Вода">Вода</option>
                <option value="Быт">Быт</option>
            </Select>
            <Input type={"text"} placeholder={"Название"}/>
            <Input type={"number"} placeholder={"Цена"}/>
            <Submit marginRight={"20px"} type={"submit"} placeholder={"Отправить"}/>
        </Form>
    )
}

export default FormShopping
