import { makeAutoObservable, action, makeObservable } from "mobx";

class Counter {

    count = 1;

    constructor() {
        makeAutoObservable(this);
    }

    inc(e) {
        e.preventDefault();
    }

    dec = () => {
        this.count = this.count - 1;
        console.log(this.count);
    }

}

export default new Counter();