import React from 'react'
import styled from 'styled-components'
import { Flex, Img } from '../globalStyles'

const ListItem = styled.li`
    display: flex;
    max-width: 732px;
    width: 100%;
    justify-content: center;
    margin-bottom: 10px;
`

const Btn = styled(Flex)`
    width: 45px;
    height: 45px;
    background: ${({background}) => background || '#fff'};
    border: 1px solid #BDBDBD;
    border-radius: 8px;
`

const Text = styled.p`
    max-width: 600px;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    background: #fff;
    border: 1px solid #BDBDBD;
    border-radius: 8px;
    padding: 0 10px;
    margin-right: 10px;
    margin-left: 10px;
`

const ShoppingListItem = () => {
    return (
        <ListItem>
            <Btn background={"#81FDB2"} justifyContent="center" alingItems="center">
                <Img src={"../Img/add.svg"}/>
            </Btn>
            <Text>Еда / Шоколад бабаевский / 500 руб.</Text>
            <Btn background="#FAA475" justifyContent="center" alingItems="center">
                <Img src={"../Img/delete.svg"}/>
            </Btn>
        </ListItem>
    )
}

export default ShoppingListItem
